  -moz-osx-font-smoothing: grayscale;
  background: rgb(52, 54, 43);
  /* background: linear-gradient(
    90deg,
    rgba(165, 167, 148, 1) 0%,
    rgba(91, 92, 84, 1) 40%
  ); */
