code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  cursor: pointer;
}

body {
  font-family: "Ubuntu", sans-serif;
}

.center {
  display: flex;
  justify-content: center;
}
.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.App {
  text-align: center;
}

.buttons > * {
  font-size: 25px;
  margin: 10px;
}

.input {
  width: 45px;
}

.goal {
  fill: rgb(211, 211, 211);
  stroke: darkgray;
  stroke-width: 2;
}

.player {
  fill: rgb(204, 130, 238);
  stroke: rgb(104, 56, 126);
  stroke-width: 2;
}

.win {
  -webkit-animation: spin 3s linear;
          animation: spin 3s linear;
  font-size: 200px;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.Tilt {
	background: rgb(211, 218, 205);
	background: -webkit-gradient(linear, left top, right top, from(rgba(211, 218, 205, 1)), color-stop(75%, rgba(152, 153, 150, 1)));
	background: linear-gradient(90deg, rgba(211, 218, 205, 1) 0%, rgba(152, 153, 150, 1) 75%);
}

